// --------------------- –§—É–Ω–∫—Ü–∏—è –∏—â–µ—Ç —É–Ω–∏–∫–∞–ª—å–Ω—ã–µ numbers –≤ –ø–æ–ª—É—á–µ–Ω–Ω–æ–º –º–∞—Å—Å–∏–≤–µ –∏ –≤–æ–∑–≤—Ä–∞–∑–∞–µ–∫—Ç –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π --------------------------------
export type uniqueItemsInArrTSType = (arr: Array<number>) => Array<number>


const uniqueItemsInArr: uniqueItemsInArrTSType = (arr) => {                          // –≤–µ—Ä–Ω–µ–º –º–∞—Å—Å–∏–≤ —Å –Ω–µ –ø–æ–≤—Ç–æ—Ä—è—é—â–∏–º–∏—Å—è –∑–Ω–∞—á–µ–Ω–∏—è–º–∏
  let result: Array<number> = [];

  for (let item of arr) {
    if (!result.includes(item)) {
      result.push(item);
    }
  }

  return result;
}

export default uniqueItemsInArr;
