// --------------------- –§—É–Ω–∫—Ü–∏—è —Å—á–∏—Ç–∞–µ—Ç –∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–æ–≤ –ø–∞–≥–æ–Ω–∞—Ü–∏–∏ –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∏—Ö –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ ----------------------
// –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–∞ —Ü–∏—Ñ—Ä –¥–ª—è —É–¥–æ–±–Ω–æ–≥–æ –∏–∑ –º–∞–ø–∞ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ —Å –ø–∞–≥–æ–Ω–∞—Ü–∏–µ–π
export type GetPaginationSquaresArrayNumbersTSType = (
                                                commentsCount: number,                  // –æ–±—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ items –≤ –º–∞—Å—Å–∏–≤–µ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–µ–≤
                                                itemsPerPage: number,                   // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ö–∞–ø–∏—Å—á–µ–π –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
                                                maxPaginationSquaresCount: number,      // –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–æ–≤ –ø–∞–≥–∏–Ω–∞—Ü–∏–∏ –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã—Ö –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
                                                currentPage: number,                    // —Ç–µ–∫—É—â–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞
                                                // clickedPage: number,                    // –∫–ª–∏–∫–Ω—É—Ç–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞
                                                firstPaginationSquareNumber: number,    // —Ü–∏—Ñ—Ä–∞ –≤ –ø–µ—Ä–≤–æ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ –ø–∞–≥–∏–Ω–∞—Ü–∏–∏ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–Ω–æ–º –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
                                                lastPaginationSquareNumber: number)     // —Ü–∏—Ñ—Ä–∞ –≤ –ø–æ—Å–ª–µ–¥–µ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ –ø–∞–≥–∏–Ω–∞—Ü–∏–∏ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–Ω–æ–º –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
                                                => Array<number>


const getPaginationSquaresArrayNumbers: GetPaginationSquaresArrayNumbersTSType = (
                                                                    commentsCount,
                                                                    itemsPerPage,
                                                                    maxPaginationSquaresCount,
                                                                    currentPage,
                                                                    // clickedPage,
                                                                    firstPaginationSquareNumber,
                                                                    lastPaginationSquareNumber) => {
    let paginationSquareArray: Array<number> = [];                                                          // –º–∞—Å—Å–∏–≤ –∏–∑ —Ü–∏—Ñ—Ä –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ–º –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å –∏ –º–∞–ø–∏—Ç—å –µ–≥–æ –Ω–∞—Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤ –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–∞—Ö –ø–∞–≥–∏–Ω–∞—Ü–∏–∏
    const totalCountPaginationSquares: number = Math.ceil(commentsCount / itemsPerPage);                    // –æ–±—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤–æ–∑–º–æ–∂–Ω—ã—Ö –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã—Ö –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–æ–≤

    if(currentPage >= firstPaginationSquareNumber && currentPage <= lastPaginationSquareNumber) {           //  –µ—Å–ª–∏ –∫–ª–∏–∫–Ω—É—Ç–∞—è —Å—Ç—Ä–∞–Ω—Ü–∞ –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –≤ –∂–∏–∞–ø–∞–∑–æ–Ω–µ –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã—Ö –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–æ–≤
        for(let i = firstPaginationSquareNumber; i <= lastPaginationSquareNumber; i++) {
            paginationSquareArray.push(i);
        }
        return paginationSquareArray;
    }

    if(currentPage < firstPaginationSquareNumber && currentPage > 0) {                                      //  –µ—Å–ª–∏ —Ü–∏—Ñ—Ä–∞ –≤ –∫–ª–∏–∫–Ω—É—Ç–æ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ –º–µ–Ω—å—à–µ —Ü–∏—Ñ—Ä—ã –≤ –ø–µ—Ä–≤–æ–º –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–Ω–æ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ, –ø–µ—Ä–≤–∞—è —Ü–∏—Ñ—Ä–∞ –±—É–¥–µ—Ç –∫–ª–∏–∫–Ω—É—Ç–∞—è, –∞ –ø–æ—Å–ª–µ–¥–Ω—è—è –∫–ª–∏–∫–Ω—É—Ç–∞—è –ø–ª—é—Å –∫–∞—É–Ω—Ç –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–≥–æ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –æ—Ç–æ–±—Ä–∞–¥–∞–µ–º—ã—Ö –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–æ–≤ –∏ –º–∏–Ω—É—Å 1
        for(let i = currentPage; i <= (currentPage + maxPaginationSquaresCount - 1); i++) {
            paginationSquareArray.push(i);
        }
        return paginationSquareArray;
    }

    if(currentPage > lastPaginationSquareNumber && currentPage <= totalCountPaginationSquares) {            //  –µ—Å–ª–∏ —Ü–∏—Ñ—Ä–∞ –≤ –∫–ª–∏–∫–Ω—É—Ç–æ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ –±–æ–ª—å—à–µ —Ü–∏—Ñ—Ä—ã –≤ –ø–æ—Å–ª–µ–¥–Ω–µ–º –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–Ω–æ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ, –ø–µ—Ä–≤–∞—è —Ü–∏—Ñ—Ä–∞ –±—É–¥–µ—Ç –∫–ª–∏–∫–Ω—É—Ç–∞—è –º–∏–Ω—É—Å –∫–∞—É–Ω—Ç –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–≥–æ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –æ—Ç–æ–±—Ä–∞–¥–∞–µ–º—ã—Ö –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–æ–≤ –∏ –ø–ª—é—Å 1, –∞ –ø–æ—Å–ª–µ–¥–Ω—è—è –∫–ª–∏–∫–Ω—É—Ç–∞—è
        for(let i = (currentPage - maxPaginationSquaresCount + 1); i <= currentPage; i++) {
            paginationSquareArray.push(i);
        }
        return paginationSquareArray;
    }

    if(currentPage <= 0) {                                                                                  // –µ—Å–ª–∏ —Ü–∏—Ñ—Ä–∞ –≤ –∫–ª–∏–∫–Ω—É—Ç–æ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ –º–µ–Ω—å—à–µ 0
        for(let i = 0; i <= maxPaginationSquaresCount; i++) {
            paginationSquareArray.push(i);
        }
        return paginationSquareArray;
    }

    if(currentPage > totalCountPaginationSquares) {                                                         // –µ—Å–ª–∏ —Ü–∏—Ñ—Ä–∞ –≤ –∫–ª–∏–∫–Ω—É—Ç–æ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ –±–æ–ª—å—à–µ –æ–±—â–µ–≥–æ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –≤–æ–∑–º–æ–∂–Ω—ã—Ö –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–æ–≤
        for(let i = (totalCountPaginationSquares - maxPaginationSquaresCount + 1); i <= totalCountPaginationSquares; i++) {
            paginationSquareArray.push(i);
        }
        return paginationSquareArray;
    }

    return paginationSquareArray;
}


export default getPaginationSquaresArrayNumbers;
// --------------------- / –§—É–Ω–∫—Ü–∏—è —Å—á–∏—Ç–∞–µ—Ç –∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–æ–≤ –ø–∞–≥–æ–Ω–∞—Ü–∏–∏ –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∏—Ö –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ --------------------