import {createStore, combineReducers, applyMiddleware} from "redux";
import thunkMiddleWare from "redux-thunk";
import { compose } from "redux";
import commentsTableReducer from "../CommentsTable/reducer";
import isAppLoadedReducer from "../AppLoaded/reducer";


let rootReducer = combineReducers({
  commentsTable: commentsTableReducer,
  isAppLoaded: isAppLoadedReducer
});

type RootReducerTSType = typeof rootReducer;                        // —Ç—É—Ç –±—É–¥–µ—Ç (state: GlobslState) => GlobalState, typeof –ø–æ—Å–ª–µ –æ—Ç—Ä–∞–±–æ—Ç–∫–∏ –æ–ø—Ä–µ–ª–¥–µ–∏—Ç —Ç–∞–∫–æ–π —Ç–∏–ø: (state: GlobslState) => GlobalState –¥–ª—è —Ñ—É–Ω–∫—Ü–∏–∏ rootStore
export type RootStateTSType = ReturnType<RootReducerTSType>          // —Ç—É—Ç –º—ã –≤—ã—Ç–∞—â–∏–ª–∏ —Ç–∏–ø GlobslState (—Ç–æ –µ—Å—Ç—å —Ç–æ —á—Ç–æ —Ñ—É–Ω–∫—É–∏—è –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç) –∏–∑ —Ç–∏–ø–∞ —Ä–µ–¥—é—Å–µ—Ä–∞

// @ts-ignore
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(rootReducer, /* preloadedState, */ composeEnhancers(
    applyMiddleware(thunkMiddleWare)
  ));                                       // —ç—Ç–æ –¥–ª—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è extension'–∞ redux dev tools –≤ Google Chrome

export default store;