import React, { useEffect, useState } from "react";
import Table from "./Table";
import {
    MapStateToPropsTSType, PropsTSType,
    MapDispatchToPropsTSType, OwnPropsTSType,
    OnPaginationSquareClickTSType, sortByEnum,
    OnTableHeaderField–°lickTSType, SortingArrowViewTSType,
    onSearchFieldChangeTSType
} from "./TableContainerTypeScriptTypes";
import { CommentTSType } from "../../BLL/commonTSTypes";
import { RootStateTSType } from "../../BLL/redux/redux";
import { commentsThunkCreator } from "../../BLL/CommentsTable/thunkCreators";
import { connect } from "react-redux";
import { getCommentsSelector, getCurrentPageSelector, getItemsPerPageSelector } from "../../BLL/CommentsTable/selectors";
import { getIsAppLoadedSelector } from "../../BLL/AppLoaded/selectors";
import AppPreloader from "../Preloader/Preloader";
import getCurrentPageComments from "../../helpers/getCurrentPageComments";
import getPaginationSquaresArrayNumbers from "../../helpers/getPaginationSquaresArrayNumbers";
import sortArrayOfObjectByFieldName from "../../helpers/sortArrayOfObjectByFieldName";
import uniqueItemsInArr from "../../helpers/uniqueItemsInArr";
import findASubstringInString from "../../helpers/findASubstringInString";
import { setCurrentPageIntoStateActionCreator } from "../../BLL/CommentsTable/actionCreators";


const TableContainer: React.FC<PropsTSType> = (props) => {
    useEffect(() => {
        props.commentsThunkCreator();
    }, [props.isAppLoaded]);

    let [firstPaginationSquareNumber, setFirstPaginationSquareNumber] = useState(1);                            // —Ü–∏—Ñ—Ä–∞ –≤ –ø–µ—Ä–≤–æ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ –ø–∞–≥–∏–Ω–∞—Ü–∏–∏
    let [lastPaginationSquareNumber, setLastPaginationSquareNumber] = useState(5);                              // —Ü–∏—Ñ—Ä–∞ –≤ –ø–æ—Å–ª–µ–¥–Ω–µ–º –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–µ –ø–∞–≥–∏–Ω–∞—Ü–∏–∏
    let [commentsSorted, setCommentsSorted] = useState(sortByEnum.notSorted);                                   // –µ—Å–ª–∏ 0 - –Ω–µ –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω, 1 - —Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é, 2 - –ø–æ —É–±—ã–≤–∞–Ω–∏—é
    let [sortByField, setSortByField] = useState<string>("");                                                   // –Ω–∞–∑–≤–∞–Ω–∏–µ –ø–æ–ª—è –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –±—É–¥–µ–º —Å–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å
    let [sortedComments, setSortedComments] = useState<Array<CommentTSType>>([{                                 // –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–µ–≤
        body: null,
        email: null,
        id: null,
        name: null,
        postId: null
    }]);
    let [searchFieldValue, setSearchFieldValue] = useState<string>("");                                         // –∑–Ω–∞—á–µ–Ω–∏–µ –≤–≤–æ–¥–∏–º–æ–µ –≤ –ø–æ–ª–µ –ø–æ–∏—Å–∫–∞ –¥–ª—è —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏
    let [arraySearchedIndexes, setArraySearchedIndexes] = useState<Array<number>>([]);                          // –º–∞—Å—Å–∏–≤ –∏–Ω–¥–µ–∫—Å–æ–≤ —Å—Ç—Ä–æ–∫ —Ç–∞–±–ª–∏—Ü—ã –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–µ–≤, –≥–¥–µ –Ω–∞—à–ª–∏ –ø–æ–¥—Å—Ç—Ä–æ–∫—É –≤–≤–µ–¥–µ–Ω–Ω—É—é –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞




    const totalCountPaginationSquares: number = Math.ceil(props.comments.length / props.itemsPerPage);                  // –æ–±—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤–æ–∑–º–æ–∂–Ω—ã—Ö –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã—Ö –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–æ–≤

    let currentPageCommentsOriginal = getCurrentPageComments(props.comments, props.currentPage, props.itemsPerPage)     // —Ñ–∏–ª—å—Ç—Ä—É–µ–º –≤—Å–µ –∫–æ–º–º–µ–Ω—Ç—ã –∏ –ø–æ–ª—É—á–∞–µ–º –∫–æ–º–º–µ–Ω—Ç—ã —Å–æ–≥–ª–∞—Å–Ω–æ —Ç–µ–∫—É—â–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤—É –∫–æ–º–º–µ–Ω—Ç–æ–≤ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ

    // ---------------------------- –ü—Ä–æ–≤–µ—Ä–∏–º –Ω—É–∂–Ω–æ –ª–∏ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–∏ -------------------------------------------------
    let currentPageComments = commentsSorted === sortByEnum.notSorted ? [...currentPageCommentsOriginal] : [...sortedComments];       // –µ—Å–ª–∏ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–∏ –Ω–µ –æ—Å—Ç–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω—ã –º–∞—Å—Å–∏–≤ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–µ–≤ –¥–ª—è –º–∞–ø–∞ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –±—É–¥–µ—Ç –æ—Ä–∏–≥–∏–Ω–∞–ª, –≤ –ø—Ä–æ—Ç–∏–≤–Ω–æ–º —Å–ª—É—á–∞–µ –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–Ω—ã–π
    // ---------------------------- / –ü—Ä–æ–≤–µ—Ä–∏–º –Ω—É–∂–Ω–æ –ª–∏ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–∏ -----------------------------------------------


    // ------------------------------ –ü—Ä–æ–≤–µ—Ä–∏–º –∏—â–µ—Ç –ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —á—Ç–æ-–∏—Ä –∏ –µ—Å–ª–∏ –∏—â–µ—Ç –∏–∑–º–µ–Ω–∏–º –º–∞—Å—Å–∏–≤ –¥–ª—è –º–∞–ø–∞ –≤ FC ----------------
    if (arraySearchedIndexes.length != 0) {
        currentPageComments = currentPageComments.filter((item, index) => {
            return arraySearchedIndexes.includes(index);
        });
    }
    if (arraySearchedIndexes.length === 0 && searchFieldValue.length != 0) {    // –µ—Å–ª–∏ –Ω–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ, –Ω–æ –∑–Ω–∞—á–µ–Ω–∏–µ –≤ –ø–æ–ª–µ –ø–æ–∏—Å–∫–∞ –µ—Å—Ç—å, —Ç–æ –µ—Å—Ç—å –ø—Ä–æ—Ç–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–∞–±–∏—Ä–∞–µ—Ç —Ç–µ–∫—Å—Ç –Ω–æ –Ω–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ
        currentPageComments = [{
            body: "–ù–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ",
            email: null,
            id: null,
            name: null,
            postId: null
        }]
    }
    // ------------------------------ / –ü—Ä–æ–≤–µ—Ä–∏–º –∏—â–µ—Ç –ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —á—Ç–æ-–∏—Ä –∏ –µ—Å–ª–∏ –∏—â–µ—Ç –∏–∑–º–µ–Ω–∏–º –º–∞—Å—Å–∏–≤ –¥–ª—è –º–∞–ø–∞ –≤ FC --------------



    // ------------------------ –ü–æ–ª—É—á–∏–º –º–∞—Å—Å–∏–≤ –∏–∑ —Ü–∏—Ñ—Ä –¥–ª—è –µ–≥–æ –º–∞–ø–∞ –≤ –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–∞—Ö –ø–∞–≥–∏–Ω–∞—Ü–∏–∏ -----------------------------------
    const paginationSquareNumbers = getPaginationSquaresArrayNumbers(
        props.comments.length,
        props.itemsPerPage,
        5,
        props.currentPage,
        firstPaginationSquareNumber,
        lastPaginationSquareNumber);
    // ------------------------ / –ü–æ–ª—É—á–∏–º –º–∞—Å—Å–∏–≤ –∏–∑ —Ü–∏—Ñ—Ä –¥–ª—è –µ–≥–æ –º–∞–ø–∞ –≤ –∫–≤–∞–¥—Ä–∞—Ç–∏–∫–∞—Ö –ø–∞–≥–∏–Ω–∞—Ü–∏–∏ ---------------------------------


    // ----------------------------------- –§—É–Ω–∫—Ü–∏—è –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –ø–∞–≥–∏–Ω–∞—Ü–∏–æ–æ–Ω—ã–π –∫–≤–∞–¥—Ä–∞—Ç–∏–∫ ----------------------------------------
    const onPaginationSquareClick: OnPaginationSquareClickTSType = (clickedPage) => {
        props.setCurrentPageIntoStateActionCreator(clickedPage);
        setFirstPaginationSquareNumber(paginationSquareNumbers[0]);
        setLastPaginationSquareNumber(paginationSquareNumbers[(paginationSquareNumbers.length - 1)]);
    }
    // ----------------------------------- / –§—É–Ω–∫—Ü–∏—è –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –ø–∞–≥–∏–Ω–∞—Ü–∏–æ–æ–Ω—ã–π –∫–≤–∞–¥—Ä–∞—Ç–∏–∫ --------------------------------------


    // -------------------------- –§—É–Ω–∫—Ü–∏—è –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –∑–∞–≥–æ–ª–æ–≤–æ–∫ –ø–æ–ª—è —Ç–∞–±–ª–∏—Ü—ã, –ø—Ä–æ–∏–∑–≤–µ–¥–µ–º —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫—É ---------------------------
    const onTableHeaderField–°lick: OnTableHeaderField–°lickTSType = (fieldName) => {
        setSortByField(fieldName);                              // —Å–æ—Ä—Ç–∏—Ä—É–µ–º –ø–æ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º—É –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä–µ –Ω–∞–∑–≤–∞–Ω–∏—é –ø–æ–ª—è

        if (commentsSorted === sortByEnum.notSorted) {           // –µ—Å–ª–∏ –¥–æ –∫–ª–∏–∫–∞ —Ç–∞–±–ª–∏—Ü–∞ –Ω–µ –±—ã–ª–∞ –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–∞
            setCommentsSorted(sortByEnum.ascending);            // –æ—Ç—Å–æ—Ä—Ç–∏—Ä—É–µ–º –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é
            const sortedComments = sortArrayOfObjectByFieldName(currentPageComments, fieldName, true)  // –µ—Å–ª–∏ —Ç—Ä–µ–±—É–µ—Ç—Å—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é
            setSortedComments(sortedComments);
        } else if (commentsSorted === sortByEnum.ascending) {    // –µ—Å–ª–∏ –¥–æ –∫–ª–∏–∫–∞ —Ç–∞–±–ª–∏—Ü–∞ –±—ã–ª–∞ –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–∞ –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é
            setCommentsSorted(sortByEnum.descending);           // –æ—Ç—Å–æ—Ä—Ç–∏—Ä—É–µ–º –ø–æ —É–±—ã–≤–∞–Ω–∏—é
            const sortedComments = sortArrayOfObjectByFieldName(currentPageComments, fieldName, false) // –µ—Å–ª–∏ —Ç—Ä–µ–±—É–µ—Ç—Å—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ —É–±—ã–≤–∞–Ω–∏—é
            setSortedComments(sortedComments);
        } else if (commentsSorted === sortByEnum.descending) {   // –µ—Å–ª–∏ –¥–æ –∫–ª–∏–∫–∞ —Ç–∞–±–ª–∏—Ü–∞ –±—ã–ª–∞ –æ—Ç—Å–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω–∞ –ø–æ —É–±—ã–≤–∞–Ω–∏—é
            setCommentsSorted(sortByEnum.ascending);            // –æ—Ç—Å–æ—Ä—Ç–∏—Ä—É–µ–º –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é
            const sortedComments = sortArrayOfObjectByFieldName(currentPageComments, fieldName, true)  // –µ—Å–ª–∏ —Ç—Ä–µ–±—É–µ—Ç—Å—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é
            setSortedComments(sortedComments);
        }
    }
    // -------------------------- / –§—É–Ω–∫—Ü–∏—è –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –∑–∞–≥–æ–ª–æ–≤–æ–∫ –ø–æ–ª—è —Ç–∞–±–ª–∏—Ü—ã, –ø—Ä–æ–∏–∑–≤–µ–¥–µ–º —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫—É -------------------------


    // ---------------- –§—É–Ω–∫—Ü–∏—è –æ–ø—Ä–µ–¥–µ–ª—è–µ—Ç –ø–æ —Å–æ—Å—Ç–æ—è–Ω–∏—é —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ –∫–∞–∫—É—é —Å—Ç—Ä–µ–ª–∫—É –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å –≤–Ω–∏–∑ –∏–ª–∏ –≤–≤–µ—Ä—Ö ---------------------
    const getSortingArrowView: SortingArrowViewTSType = (sortAscending) => {
        if (sortAscending === sortByEnum.notSorted) return null;
        if (sortAscending === sortByEnum.ascending) return <span>&#9660;</span>;      // –µ—Å–ª–∏ –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é –≤–µ—Ä–Ω–µ–º —Å—Ç—Ä–µ–ª–∫—É –≤–Ω–∏–∑
        if (sortAscending === sortByEnum.descending) return <span>&#9650;</span>;     // –µ—Å–ª–∏ –ø–æ —É–±—ã–≤–∞–Ω–∏—é –≤–µ—Ä–Ω–µ–º —Å—Ç—Ä–µ–ª–∫—É –≤–≤–µ—Ä—Ö
    }
    // ---------------- / –§—É–Ω–∫—Ü–∏—è –æ–ø—Ä–µ–¥–µ–ª—è–µ—Ç –ø–æ —Å–æ—Å—Ç–æ—è–Ω–∏—é —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ –∫–∞–∫—É—é —Å—Ç—Ä–µ–ª–∫—É –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å –≤–Ω–∏–∑ –∏–ª–∏ –≤–≤–µ—Ä—Ö -------------------


    // ----------------------------- –§—É–Ω–∫—Ü–∏—è –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –ø–æ–∏—Å–∫–∞ -----------------------------
    const onSearchFieldChange: onSearchFieldChangeTSType = (value) => {
        setSearchFieldValue(value);                                     // –∑–∞–ø–∏—à–µ–º –Ω–æ–≤–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –∏–∑ –ø–æ–ª—è –≤–≤–æ–¥–∞ –ø–æ–∏—Å–∫–∞ –¥–ª—è –µ–≥–æ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ (—Ñ–ª–∞–∫—Å)
        let commentsForSearch = commentsSorted === sortByEnum.notSorted ? [...currentPageCommentsOriginal] : [...sortedComments];

        let indexesArray: Array<number> = [];                                       // –º–∞—Å—Å–∏–≤ –∏–Ω–¥–µ–∫—Å–æ–≤ —Å—Ç—Ä–æ–∫ —É–¥–æ–≤–ª–µ—Ç–≤–æ—Ä–∏–≤—à–∏—Ö –ø–æ—Å–∏–∫—É
        commentsForSearch.forEach((item, i) => {
            if (item.body != null) {                                                // null –Ω–µ –ø–æ–¥–æ–π–¥–µ—Ç –¥–ª—è —ç—Ç–æ–π —Ñ—É–Ω–∫—Ü–∏–∏, –µ–≥–æ –Ω–µ–ª—å–∑—è —Å–ø–ª–∏—Ç–∞—Ç—å, –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —Ç–æ–ª—å–∫–æ —Å—Ç—Ä–æ–∫–∞
                let returnedFoundObj = findASubstringInString(item.body, value);
                if (returnedFoundObj.isSearchValueFinded) {                         // –µ—Å–ª–∏ –Ω–∞—à–ª–∏ –ø–æ–¥—Å—Ç—Ä–æ–∫—É –≤ —Å—Ç—Ä–æ–∫–µ
                    indexesArray.push(i)                                            // –∑–∞–ø—É—à–µ–º –∏–Ω–¥–µ–∫—Å —Å—Ç—Ä–æ–∫–∏ –≤ –º–∞—Å—Å–∏–≤
                }
            }
            if (item.email != null) {                                               // null –Ω–µ –ø–æ–¥–æ–π–¥–µ—Ç –¥–ª—è —ç—Ç–æ–π —Ñ—É–Ω–∫—Ü–∏–∏, –µ–≥–æ –Ω–µ–ª—å–∑—è —Å–ø–ª–∏—Ç–∞—Ç—å, –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —Ç–æ–ª—å–∫–æ —Å—Ç—Ä–æ–∫–∞
                let returnedFoundObj = findASubstringInString(item.email, value);
                if (returnedFoundObj.isSearchValueFinded) {                         // –µ—Å–ª–∏ –Ω–∞—à–ª–∏ –ø–æ–¥—Å—Ç—Ä–æ–∫—É –≤ —Å—Ç—Ä–æ–∫–µ
                    indexesArray.push(i)                                            // –∑–∞–ø—É—à–µ–º –∏–Ω–¥–µ–∫—Å —Å—Ç—Ä–æ–∫–∏ –≤ –º–∞—Å—Å–∏–≤
                }
            }
            if (item.name != null) {                                                // null –Ω–µ –ø–æ–¥–æ–π–¥–µ—Ç –¥–ª—è —ç—Ç–æ–π —Ñ—É–Ω–∫—Ü–∏–∏, –µ–≥–æ –Ω–µ–ª—å–∑—è —Å–ø–ª–∏—Ç–∞—Ç—å, –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —Ç–æ–ª—å–∫–æ —Å—Ç—Ä–æ–∫–∞
                let returnedFoundObj = findASubstringInString(item.name, value);
                if (returnedFoundObj.isSearchValueFinded) {                         // –µ—Å–ª–∏ –Ω–∞—à–ª–∏ –ø–æ–¥—Å—Ç—Ä–æ–∫—É –≤ —Å—Ç—Ä–æ–∫–µ
                    indexesArray.push(i)                                            // –∑–∞–ø—É—à–µ–º –∏–Ω–¥–µ–∫—Å —Å—Ç—Ä–æ–∫–∏ –≤ –º–∞—Å—Å–∏–≤
                }
            }
            if (item.id != null) {                                                  // null –Ω–µ –ø–æ–¥–æ–π–¥–µ—Ç –¥–ª—è —ç—Ç–æ–π —Ñ—É–Ω–∫—Ü–∏–∏, –µ–≥–æ –Ω–µ–ª—å–∑—è —Å–ø–ª–∏—Ç–∞—Ç—å, –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —Ç–æ–ª—å–∫–æ —Å—Ç—Ä–æ–∫–∞
                let returnedFoundObj = findASubstringInString(item.id.toString(), value);
                if (returnedFoundObj.isSearchValueFinded) {                         // –µ—Å–ª–∏ –Ω–∞—à–ª–∏ –ø–æ–¥—Å—Ç—Ä–æ–∫—É –≤ —Å—Ç—Ä–æ–∫–µ
                    indexesArray.push(i)                                            // –∑–∞–ø—É—à–µ–º –∏–Ω–¥–µ–∫—Å —Å—Ç—Ä–æ–∫–∏ –≤ –º–∞—Å—Å–∏–≤
                }
            }
            if (item.postId != null) {                                              // null –Ω–µ –ø–æ–¥–æ–π–¥–µ—Ç –¥–ª—è —ç—Ç–æ–π —Ñ—É–Ω–∫—Ü–∏–∏, –µ–≥–æ –Ω–µ–ª—å–∑—è —Å–ø–ª–∏—Ç–∞—Ç—å, –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —Ç–æ–ª—å–∫–æ —Å—Ç—Ä–æ–∫–∞
                let returnedFoundObj = findASubstringInString(item.postId.toString(), value);
                if (returnedFoundObj.isSearchValueFinded) {                         // –µ—Å–ª–∏ –Ω–∞—à–ª–∏ –ø–æ–¥—Å—Ç—Ä–æ–∫—É –≤ —Å—Ç—Ä–æ–∫–µ
                    indexesArray.push(i)                                            // –∑–∞–ø—É—à–µ–º –∏–Ω–¥–µ–∫—Å —Å—Ç—Ä–æ–∫–∏ –≤ –º–∞—Å—Å–∏–≤
                }
            }
        });

        setArraySearchedIndexes(uniqueItemsInArr(indexesArray));                // –∑–∞–ø–∏—à–µ–º –≤ –ª–æ–∫–∞–ª—å–Ω—ã–π —Å—Ç–µ–π—Ç –º–∞—Å—Å–∏–≤ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö(–±–µ–∑ –ø–æ–≤—Ç–æ—Ä–µ–Ω–∏–π) –∏–Ω–¥–µ–∫—Å–æ–≤ —Å—Ç—Ä–æ–∫ –æ—Ä–∏–≥–∏–Ω–∞–ª—å–Ω–æ–≥–æ –º–∞—Å—Å–∏–≤–∞
    }
    // ----------------------------- / –§—É–Ω–∫—Ü–∏—è –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –ø–æ–∏—Å–∫–∞ ---------------------------



    if (props.isAppLoaded) {                             // –µ—Å–ª–∏ –æ—Ç–≤–µ—Ç –æ—Ç —Å–µ—Ä–≤–µ—Ä–∞ –µ—â–µ –Ω–µ –ø–æ–ª—É—á–µ–Ω –ø–æ–∫–∞–∂–µ–º –ø—Ä–µ–ª–æ–∞–¥–µ—Ä
        return <Table
            comments={props.comments}
            currentPageComments={currentPageComments}
            paginationSquareNumbers={paginationSquareNumbers}
            currentPage={props.currentPage}
            totalCountPaginationSquares={totalCountPaginationSquares}
            onPaginationSquareClick={onPaginationSquareClick}
            onTableHeaderField–°lick={onTableHeaderField–°lick}
            getSortingArrowView={getSortingArrowView}
            sortByField={sortByField}
            commentsSorted={commentsSorted}
            searchFieldValue={searchFieldValue}
            onSearchFieldChange={onSearchFieldChange}
        />
    } else {
        return <AppPreloader />
    }
}


const mapStateToProps = (state: RootStateTSType) => {
    return {
        comments: getCommentsSelector(state),           // –≤—Å–µ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–∏
        isAppLoaded: getIsAppLoadedSelector(state),
        currentPage: getCurrentPageSelector(state),
        itemsPerPage: getItemsPerPageSelector(state)
    }
}

const mapDispatchToProps = {
    commentsThunkCreator,
    setCurrentPageIntoStateActionCreator,
}

export default connect<MapStateToPropsTSType, MapDispatchToPropsTSType, OwnPropsTSType, RootStateTSType>(mapStateToProps, mapDispatchToProps)(TableContainer);;