// ---------------------------------------------- –§—É–Ω–∫—Ü–∏—è –∏—â–µ—Ç –ø–æ–¥—Å—Ç—Ä–æ–∫—É –≤ —Å—Ç—Ä–æ–∫–µ –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –æ–±—ä–µ–∫—Ç ---------------------------------------
export type findASubstringInStringReturnedObjTStype = { isSearchValueFinded: boolean, startFoundIndexes: Array<number>, endFoundIndexes: Array<number> }        // –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—ã–π –æ–±—ä–µ–∫—Ç
export type findASubstringInStringTSType = (str: string, searchValue:string) => findASubstringInStringReturnedObjTStype

const findASubstringInString: findASubstringInStringTSType = (str, searchValue) => {            // –ø–∞—Ä–∞–º–µ—Ç—Ä—ã: str - —Å—Ç—Ä–æ–∫–∞ –≥–¥–µ –±—É–¥–º –∏—Å–∫–∞—Ç—å, searchValue - –ø–æ–¥—Å—Ç—Ä–æ–∫–∞ –∫–æ—Ç–æ—Ä—É—é –±—É–¥–µ–º –∏—Å–∫–∞—Ç—å
    const splitedStr =  str.split("");                 // —Å–ø–∏—Ç–Ω–µ–º –≤–∑–æ–¥—è—â—É—é —Å—Ç—Ä–æ–∫—É –≥–¥–µ –±—É–¥–µ–º –∏—Å–∫–∞—Ç—å
    const splitedSearchValue = searchValue.split(""); // —Å–ø–ª–∏—Ç–Ω–µ–º —Å—Ç—Ä–æ–∫—É –∫–æ—Ç–æ—Ä—É—é –±—É–¥–µ–º –∏—Å–∫–∞—Ç—å

    let returnedObj: findASubstringInStringReturnedObjTStype = {                               // –æ–±—ä–µ–∫—Ç –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ–º –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å
        isSearchValueFinded: false,                     // –µ—Å–ª–∏ –Ω–∞—à–ª–∏ –∏—Å–∫–æ–º—É—é –ø–æ–¥—Å—Ç—Ä–æ–∫—É –∏–∑–º–µ–Ω–∏–º –Ω–∞ true
        startFoundIndexes: [],                          // –∏–Ω–¥–µ–∫—Å—ã –Ω–∞—á–∞–ª—å–Ω–æ–π –±—É–¥–∫–≤—ã –Ω–∞–π–¥–µ–Ω–Ω–æ–π –ø–æ–¥—Å—Ç—Ä–æ–∫–∏ –≤ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º –º–∞—Å—Å–∏–≤–µ
        endFoundIndexes: []                              // –∏–Ω–¥–µ–∫—Å—ã –∫–æ–Ω–µ—á–Ω–æ–π –±—É–¥–∫–≤—ã –Ω–∞–π–¥–µ–Ω–Ω–æ–π –ø–æ–¥—Å—Ç—Ä–æ–∫–∏ –≤ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º –º–∞—Å—Å–∏–≤–µ, –±—É–¥–µ–º –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –¥–ª—è –ø–æ–¥—Å–≤–µ—Ç–∫–∏ –Ω–∞–π–¥–µ–Ω–Ω–æ–≥–æ —Ç–µ–∫—Å—Ç–∞ –≤ UI
    };

    splitedStr.forEach((element, i) => {
        let slicedArr = splitedStr.slice(i, (splitedSearchValue.length + i));                               // –ø—Ä–∏–º–µ–Ω–∏–º —Å–ª–∞–π—Å –¥–ª—è –∫–∞–∂–¥–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞, —á—Ç–æ–±—ã –ø–æ–ª—É—á–∏—Ç—å –º–∞—Å—Å–∏–≤ —Ç–∞–∫–æ–π –∂–µ –¥–ª–∏–Ω—ã –∫–∞–∫ –∏ –∏—â–µ–º, —á—Ç–æ–±—ã —Å—Ä–∞–≤–Ω–∏—Ç—å —Å–ª–∞–π—Å–Ω—É—Ç—ã–π –º–∞—Å—Å–∏–≤ –∏ –º–∞—Å—Å–∏–≤ –∫–æ—Ç–æ—Ä—ã–π –∏—â–µ–º. –ø–µ—Ä–≤—ã–π –∞—Ä–≥—É–º–µ–Ω—Ç –≤ —Å–ª—ã–π—Å –∏–Ω–¥–µ–∫—Å —Ü–∏–∫–ª–∞, —á—Ç–æ–±—ã —Å–ª–∞–π—Å–∏—Ç—å –Ω–∞—á–∏–Ω–∞—è —Å –∫–∞–∂–¥–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –º–∞—Å—Å–∏–≤–∞, –≤—Ç–æ—Ä–æ–π –∞—Ä–≥—É–º–µ–Ω—Ç –¥–ª–∏–Ω–∞ —Å—Ç—Ä–æ–∫–∏ –¥–ª—è –ø–æ–∏—Å–∫–∞
        let joinedSlicedArr = slicedArr.join("");                                                           // —Å–æ–µ–¥–∏–Ω–∏–º —Å–ª–∞–π—Å–Ω—É—Ç—ã–π –º–∞—Å—Å–∏–≤ –≤ —Å—Ç—Ä–æ–∫—É –¥–ª—è –µ–≥–æ —Å—Ä–∞–≤–Ω–µ–Ω–∏—è —Å–æ —Å—Ç—Ä–æ–∫–æ–π –∫–æ—Ç–æ—Ä—É—é –∏—â–µ–º

        if (joinedSlicedArr === searchValue) {                                                              // –µ—Å–ª–∏ —Å—Ç—Ä–æ–∫–∞ —Å–ª–µ–ø–ª–µ–Ω–Ω–∞—è –∏–∑ –º–∞—Å—Å–∏–≤–∞ –≤—ã—Ä–µ–∑–∞–Ω–Ω–æ–≥–æ –∏–∑ –≤—Ö–æ–¥—è—â–µ–π —Å—Ç—Ä–æ–∫–∏ –¥–ª–∏–Ω–Ω–æ–π —Ä–∞–≤–Ω–æ–π –¥–ª–∏–Ω–µ —Å—Ç—Ä–æ–∫–∏ –¥–ª—è –ø–æ–∏—Å–∫–∞ —Ä–∞–≤–Ω–æ —Å—Ç—Ä–∫–µ –ø–æ–∏—Å–∫–∞, —Ç–æ –µ—Å—Ç—å –º—ã –Ω–∞—à–ª–∏ –Ω—É–∂–Ω—É—é –Ω–∞–º –ø–æ–¥—Å—Ä–æ–∫—É
            returnedObj = {                                                                                 // –≤–Ω–µ—Å–µ–º –≤ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—ã–π –æ–±—ä–µ–∫—Ç
                ...returnedObj,                                                                             // –∫–æ–ø–∏—é –æ–±–µ–∫—Ç–∞, –º–æ–∂–µ—Ç —Ç–∞–º —É–∂–µ –µ—Å—Ç—å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –Ω—É–∂–Ω–∞—è –Ω–∞–º, –Ω–µ –ø–µ—Ä–µ–∑–∞–ø–∏—Å—ã–≤–∞–µ–º –∏–º–µ—é—â–∏–π—Å—è –æ–±—ä–µ–∫—Ç
                isSearchValueFinded: true,                                                                  // –ø–æ–¥—Å—Ç—Ä–æ–∫–∞ –Ω–∞–π–¥–µ–Ω–∞. –ï—Å–ª–∏ true —É–∂–µ –∑–∞–ø–∏—Å–∞–Ω–∞ –ø–µ—Ä–µ–∑–∞–ø–∏—Å—ã–≤–∞—Ç—å –Ω–µ –±—É–¥–µ–º
                startFoundIndexes: [...returnedObj.startFoundIndexes, i],                                   // –º–∞—Å—Å–∏–≤ –∏–Ω–¥–µ–∫—Å–æ–≤ –Ω–∞—á–∞–ª—å–Ω–æ–π –±—É–¥–∫–≤—ã –Ω–∞–π–¥–µ–Ω–Ω–æ–π –ø–æ—Å—Ç—Ä–æ–∫–∏ –≤ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º –º–∞—Å—Å–∏–≤–µ
                endFoundIndexes: [...returnedObj.endFoundIndexes, (i + splitedSearchValue.length - 1)]      // –º–∞—Å—Å–∏–≤ –∏–Ω–¥–µ–∫—Å–æ–≤ –∫–æ–Ω–µ—á–Ω–æ–π –±—É–¥–∫–≤—ã –Ω–∞–π–¥–µ–Ω–Ω–æ–π –ø–æ—Å—Ç—Ä–æ–∫–∏ –≤ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º –º–∞—Å—Å–∏–≤–µ, –±—É–¥–µ–º –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –¥–ª—è –ø–æ–¥—Å–≤–µ—Ç–∫–∏ –Ω–∞–π–¥–µ–Ω–Ω–æ–≥–æ —Ç–µ–∫—Å—Ç–∞ –≤ UI
            }
        }
    });

    return returnedObj;
}

export default findASubstringInString;
// ---------------------------------------------- / –§—É–Ω–∫—Ü–∏—è –∏—â–µ—Ç –ø–æ–¥—Å—Ç—Ä–æ–∫—É –≤ —Å—Ç—Ä–æ–∫–µ –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –æ–±—ä–µ–∫—Ç -------------------------------------